.contact-form-content{
    width: 100%;
}
form{
    display: flex;
    flex-direction: column;
    grid-gap: 1.5rem;
}
.name-container{
    display: flex;
    align-items: center;
    grid-gap: 1.5rem;
}

form input,
form textarea{
    flex: 1;
    width: 100%;
    font-size: 0.9rem;
    color: #fff;
    background-color: #040f11;
    border-radius: 0.6rem;
    border:  1.5px solid #00bcd4;
    padding: 1rem;
}

form button{
    font-size: 1rem;
    font-weight: 500;
    color: #fff;
    border: 1.5px solid #00bcd4;
    border-radius: 0.6rem;
    background: linear-gradient(90deg,#4dd0e1 0%,#00838f 100%);
    padding: 0.7rem;
    transition: all 0.3s ease;
    cursor: pointer;
}
form button:hover{
    color: #00bcd4;
    background: transparent;
}

@media(max-width:450px){
    .name-container{
        flex-direction: column;
        align-items: flex-start;
    }
    form input,
    form textarea{
        padding: 0.7rem;
        border-radius: 0.3rem;
    }
    form button{
        font-size: 0.9rem;
        padding: 0.7rem;
        border-radius: 0.3rem;
    }
}