 .profile-container{
    display: flex;
    align-items: center;
    position: relative;
 }    
 
 .profile-container::after,
 .profile-container::before{
    content: " ";
    width: 28.125rem;
    height: 28.125rem;
    border-radius: 28.125rem;
    background: #b2ebf2;
    position: absolute;
    z-index: -1;
    filter: blur(225px) ;

 }
 .profile-container::after{
    top: -3rem;
    left: -5rem;
 }
.profile-container::before{
    bottom: 2rem;
    right: 0rem;
}
.profile-content{
    flex: 1;
}
.profile-content h2{
    font-size: 3.4rem;
    font-weight: 50;
    line-height: 5rem;
    margin-bottom: 1rem;
}
.profile-content p{
    width: 80%;
    font-size: 1rem;
    font-weight: 400;
    line-height: 2rem;
}


.profile-img{
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    margin-top: 5rem;
}
.profile-img > div {
    display: flex;
    align-items: flex-end;
    gap: 2rem;
}

.profile-img > div > img{
    width: 25rem;
    transition: all 0.3s ease;
} 

.tech-icon{
    width: 5rem;
    height: 5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    border-radius: 0.65rem;
    border: 1px solid #00bcd4;
}
.tech-icon img{
    width: 3rem;
    height: auto;
    transition: all 0.3s ease;

}

.profile-img > div > img:hover,
.tech-icon img:hover{
    transform: translateY(-0.5rem);
}


@media(max-width:1025px){
    .profile-content h2{
        font-size: 3rem;
        line-height: 4rem;

    }
    .hero-content p{
        font-size: 0.9rem;
        line-height: 1.6rem;
    }
    .hero-img > div > img{
        width: 20rem;
    }
    .tech-icon{
        width: 4rem;
        height: 4rem;
    }
    .tech-icon img{
        width: 2.5rem;
    }
    
}

@media(max-width:768px){
    .profile-container{
        flex-direction: column;

    }
    .profile-img{
        margin: 2rem 0 2rem 0;

    }
    .profile-container h2{
        margin-top: 3rem;
    }
    .profile-content h2,
    .profile-content p{
        width: auto;
        text-align: center;
    }
    .profile-container::after,
    .profile-container::before{
        content: " ";
        width: 18rem;
        height: 18rem;
    }
    .profile-container::after{
        top : 0rem;
        left: 0rem;
    }
    .profile-container::before{
        bottom: 2rem;
        right: 0rem;
    }
}

@media(max-width:600px){
    .profile-content h2{
        font-size: 2.2rem;
        line-height: 3rem;

    }
    .profile-content p{
        font-size: 0.8rem;
        line-height: 1.3rem;
    }
    .profile-img{
        gap: 1rem;

    }
    .profile-img > div {
        gap: 1rem;

    }
    .profile-img > div > img{
        width: 16rem;
    }
    .tech-icon{
        width: 3.3rem;
        height: 3.3rem;

    }
    .tech-icon img{
        width: 2rem;
    }
}